*{
    padding: 0;
    margin: 0;
}
body{
    min-width: 868px;
}

.coc{
    margin: 0 120px;
}

header{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
    align-items: center;
}
header ul{
    list-style: none;
    display: flex;
    gap: 20px;
}

header ul li a{
    text-decoration: none;
    color: black;
}

/* hero */
.home{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.home-img{
    display: grid;
    grid-template-areas: 
    "overlap"
    ;
}
.vector{
  grid-area: overlap;
  width: 500px;
  height: 580px;
}

.man{
 grid-area: overlap;
 height: 580px;
 padding-left: 130px;
}

.home h1{
    font-size: 50px;
    
    
}

.home p{
    padding-top: 30px;
}

/* logos */

.logos{
  display: flex ;
  justify-content: space-around;
 
  
  padding: 50px 20px ;
  
}

.logo-items img{
    width: 130px;
    padding: 30px;
}

/* feauture */

.feature{
    background-color: #0077C0;
    color: white;
    
}

.f-container{
    margin: 0 120px;
    display: grid;
    grid-template-rows: 200px auto
}
.f-items{
    display: flex;
}

    .f-items img{
        background-color: skyblue;
        border-radius: 100%;
        padding: 30px;
        
    }

    .f-items h3, .f-items p {
        padding: 20px 0px;
    }
    .f-items p{
        padding-bottom: 50px;
        text-align: left;
    }
    .f-text{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .f-text h1{
        font-size: 40px;
        max-width: 400px;
        
    }

/* tutorial */


.tutorial{
    margin: 80px 120px;
    
}
.tut{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.tut-text{
    display: grid;
    justify-content: start;
    align-content: center;
    grid-template-rows: 120px auto;
    
}
.image{
    width: 350px;
}
.tut-text ol li {

    display: flex;
    align-items: center;
    list-style: none;
    padding-bottom: 20px ;
}
.tut-text ol li img {
   padding-right: 20px;
    
}

.tut-text h1{
    font-size: 30px;
     width: 400px;
}

.wakanda{
    display:none;
}

.explore{
    background-color: skyblue;
}
.exp-container{
    margin: 0px 120px;

}

.exp-container{
    display: flex;
    justify-content: space-between;

}
.handsome{
    height: 500px;
}
.exp-text{
    display: grid;
    justify-content: center;
    align-content: center;
}
.exp-text h1{
    font-size: 40px;
    max-width: 600px;
    padding: 10px 0px;
    text-align: left;
}
.exp-text p{
      max-width: 600px;
    padding: 10px 0px;
   
}

.circle{
    grid-area: overlap;
    justify-self: center;
}


.handsome{
    grid-area: overlap;
    justify-self: center;
}

.Card{
 grid-area: overlap;
 justify-self: center;
 padding-left: 200px;
 width: 220px;

}
.Card2{
    grid-area: overlap;
    justify-self: center;
    padding-top: 250px;
    padding-right: 300px;
    width: 220px;
}


.exp-image{
    display: grid;
    grid-template-areas: "overlap";
}







































@media only screen and (max-width: 768px){
    .home{
        flex-direction: column-reverse;
        gap: 50px;
    }

    .tutorial{
        margin: 80px 50px;
        
    }

    .exp-container{
        margin: 0px 50px;
    
    }
    .coc{
        margin: 0 50px;
    }
    .f-container{
        margin: 0 50px;
        display: grid;
        grid-template-rows: 200px auto
    }

    .f-items{
        display: grid;
        grid-template-columns: none;
        grid-template-rows: repeat(3,1fr);
        
        gap: 0px;
    }
    .f-container{
        grid-template-rows: 200px auto;
    }
}

















@media only screen and (max-width:1000px){
    .image{
        width: 200px;
        padding-bottom: 20px;
    }
    .frame{
        display: flex;
        justify-content: center;
        align-items: flex-end;
        
    }

    
    .tut-text h1{
        font-size: 40px;
         width: 400px;
        display: none;
    }
    .wakanda{
        display: block;
        text-align: center;
        padding-top: 30px;
    }
    .tut-text{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .tutorial{
        margin: 40px 120px;
        
    }
    .tut{
        display: flex;
        justify-content: center;
        gap: 20px;
    }
    .exp-container{
        display: flex;
        flex-direction: column-reverse;   
        
    }
    .handsome{
       display: block;
       margin: 0 auto;    
        height: 500px;
    }
    .exp-text h1, .exp-text p{
        
        width: 100%;
      
    }
}