*{
    padding: 0;
    margin: 0;
}

body{
    min-width:420px;
}



.nav{
    display: flex;
    justify-content: space-between;
    height: 80px;
    padding: 0 50px;
    align-items: center;
    flex-wrap: wrap;
    position: sticky;
    top: 0px;
    
}

.nav::before{
    background-color: black;
    content: "";
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.3;
}

.nav-items{
    display: flex;
  
}
.nav ul li{
 
 text-align: center;
}
.nav ul li a{
 
    text-decoration: none ;
    color: black;
   }

   .nav ul li a:hover{
     background-color: white;
     color: black;
     border-radius: 10%;
    text-decoration: none ;
    color: black;
   }
.nav ul{
    list-style: none;
    gap: 20px;
    font-size: 20px;
}

#menu{
    display: none;
}




@media only screen and (max-width: 700px){


    

    .notdisplay{
       transform: translateX(100%);
      
    }

    #menu{
        
        display: block;
    }

 .nav-items{
   
    transition-duration: 1s;
    
    
     position: fixed;
     flex-direction: column;
     width: 30%;
     background-color:snow;
     right: 0;
     top: 80px;
     
 }

.services{
    flex-direction: column;
}

/* .logos{
    flex-direction: column;
    align-items: center;
} */

}
.home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 575px;
   
}

.home::before{
   position: absolute;
   top: -100px;
   left: 0px;
   width: 100%;
   height: 120%;
   opacity: 0.9;
   z-index: -1;
   content: ""; 
   background: url('food.jpg') no-repeat center center/cover;

}




h1, h2{
    padding: 20px;
    text-align: center;
}
p{
    text-align: center;
    width: 80%;

}

.services{
    display: flex;
    gap: 40px;
    padding-bottom: 20px;
}

.serv{
    background-color:beige;
}

.item{
    display: flex;
    padding-bottom: 30px;
  align-items: center;
  flex-direction: column;
  background-color: #F0F8FF;
  border-radius: 40px;
 flex: 1;
 
}

.item  img{
    height: 160px;
    margin: auto;
    display: block;
    padding-top: 20px;
   

}


.bulk-odering img{
    padding-top: 20px;
    margin-top: 3px;
    margin-bottom: 4px;
    height: 130px;
    border-radius: 30%;
}

.Our-clients{
    position: relative;
}

.Our-clients::before{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    opacity: 0.9;
    z-index: -1;
    content: ""; 
    background: url('client1.jpg') no-repeat center center/cover;
}


.logos{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-items: center;
    gap: 40px;
    padding-bottom: 15px;
   
}

.logo img{
    min-width: 75px;
    max-width:150px
  
}

.logo{
    display: flex;
    justify-content: center;
    align-items: center;
  
}

@media only screen and (min-width: 700px){





.logos{
    grid-template-rows: none;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

}

.contact-us{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 34px;
    position: relative;
    padding-top: 34px;
}
.contact-us input, 
.contact-us textarea{
    width: 100%;
    padding: 0.5rem;
    border-radius: 9px;
    font-size: 1.1rem;
} 

.contact-us::before{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    opacity: 0.9;
    z-index: -1;
    content: ""; 
    background: url('contact.jpg') no-repeat center center/cover;
}


.contact-us form{
    width: 40%;
}

.btn {
    background: #3498db;
    background-image: -webkit-linear-gradient(top, #3498db, #2980b9);
    background-image: -moz-linear-gradient(top, #3498db, #2980b9);
    background-image: -ms-linear-gradient(top, #3498db, #2980b9);
    background-image: -o-linear-gradient(top, #3498db, #2980b9);
    background-image: linear-gradient(to bottom, #3498db, #2980b9);
    -webkit-border-radius: 28;
    -moz-border-radius: 28;
    border-radius: 28px;
    font-family: Georgia;
    color: #ffffff;
    font-size: 20px;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
  }
  
  .btn:hover {
    background: #9fd4f5;
    background-image: -webkit-linear-gradient(top, #9fd4f5, #8daab8);
    background-image: -moz-linear-gradient(top, #9fd4f5, #8daab8);
    background-image: -ms-linear-gradient(top, #9fd4f5, #8daab8);
    background-image: -o-linear-gradient(top, #9fd4f5, #8daab8);
    background-image: linear-gradient(to bottom, #9fd4f5, #8daab8);
    text-decoration: none;
  }