
*{
    padding: 0;
    margin: 0;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}



.first{
    background-color: blueviolet;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 100px;
    min-width: 400px;
  
}

.first div{
    max-width:40%;  
    min-width: 400px;
    margin: 100px 0px;
}

.firsttext{
    text-align: center;
    
}

.firsttext h1{
    margin-left: 0px;
    text-align: left;
}

.firsttext h5{
    font-size: 60px;
    padding: 10px 0px;
    text-align: left;
    margin-left: 25px;
}

.firsttext p{
    line-height: 25px;
    padding: 0 20px;
}

.firsttext button {
    margin-left: -60px;
}

.firsttext{
    color: white;
    
    
}




.firstimg img{
    border-radius: 100%;
    width: 100%;

}
.btn{
    font-size: 20px;
    padding: 10px 65px;
    color: white;
    background-color:deeppink;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 20px 0px;
    border-style: none;
   
}

.btn:hover{
    color:blueviolet;
    background-color: white;
}

.second{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 20px;
    background: url(second.jpg);
    padding: 200px 100px;
    padding-bottom: 100px;
    
    
}
.secondd{
    min-width: 400px;
}

.second div {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 20px;
    text-align: center;
    
}

.second img{
    padding: 10px;
    margin: 20px;
    width: 64px;
    background-color:plum;
    border-radius: 100%;
    
}
.second p{
    padding: 20px 20px;
}
.second h3{
    color:violet;
}
.second a{
    padding-bottom: 30px;
}
.second-2{
    position: relative;
    bottom: 100px;
}
.second-4{
    position: relative;
    bottom: 100px;
}

.third{
    position: relative;
    display: grid;
    padding: 0px 15%;
    padding-top: 15%;
    padding-bottom: 450px;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(2,0.5fr);
    row-gap: 50px;
    column-gap: 50px;
    
}

.third img{
   width: 100%;
   height: 100%;
}

.pink{
    position: absolute;
    height: 70%;
    width: 60%;
    left: 20%;
    top: 10%;
    background-color:mediumvioletred;
    z-index: -1;
    
}
.portfolio{
    position: absolute;
    background-color: blueviolet;
    max-height: 450px;
    max-width: 450px;
    top: 70%;
    left: 60%;
    margin-right: 50px;
    display: grid;
    justify-content: center;
    align-content: center;
    margin-bottom: 400px;
}
.portfolio h1, .portfolio p{
    color: white;
    padding: 10px 20px;
    line-height: 30px;

}

.Lbtn{
    font-size: 20px;
    padding: 10px 0px;
    color: white;
    background-color:deeppink;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 20px 20px;
    border-style: none;
    width: 200px;
   
}

.Lbtn:hover{
    color:blueviolet;
    background-color: white;
}


@media only screen and (max-width:900px){
    .second{
        display:grid;
        grid-template-columns: auto;    
        grid-template-rows: repeat(5,1fr);
        justify-content: center;
        align-items: center;
    }
    .first div{
        max-width:40%;  
        min-width: 400px;
        margin: 50px 0px;
    }

    .first{
        gap:0px;    
    }
    .second-2{
        position: static;
        
    }
    .second-4{
        position: static;
       
    } 
    
         
}


@media only screen and (max-width:420px){
    .third{
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: repeat(6,1fr);
        row-gap: 50px;
        column-gap: 50px;
        padding: 100px 0px;
        padding-left: 50px;
        padding-right: 50px;
    }
    
    .third img{
       width: 100%;
       height: 100%;
       min-width: 300px;
    }
    
    .pink{
     display: none;   
    }
    .portfolio{
        background-color: blueviolet;
        max-height: 450px;
        max-width: 450px;
        display: grid;
        justify-content: center;
        align-content: center;
        position: static;
    }
    .portfolio h1, .portfolio p{
        color: white;
        padding: 10px 20px;
        line-height: 30px;
    
    }
      
}

@media only screen and (min-width:421px) and (max-width:768px){

    .third{
        position: relative;
        display: grid;
        padding: 0px 15%;
        padding-top: 15%;
        padding-bottom: 450px;
        grid-template-columns: repeat(2,1fr);
        grid-template-rows: repeat(3,1fr);
        row-gap: 50px;
        column-gap: 50px;
        
    }
    
    .third img{
       width: 100%;
       height: 100%;
    }
    
    .pink{
        position: absolute;
        height: 70%;
        width: 60%;
        left: 20%;
        top:5%;
        background-color:mediumvioletred;
        z-index: -1;
    }
    .portfolio{
        position: absolute;
        background-color: blueviolet;
        display: grid;
        grid-column-end: span 2;
        justify-content: center;
        align-content: center;
        left:40%;
        margin-bottom: 40px;
    }
    .portfolio h1, .portfolio p{
        color: white;
        padding: 10px 20px;
        line-height: 30px;
    
    }
}

