nav{
    background-color: black;
    display: flex;
    justify-content: space-around;
    height: 6rem;
    align-items: center;
}

nav a{
    text-decoration: none;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    border-radius: 15%;
    color: azure;
}

nav a:hover{
    background-color:white;
    color: black;
}

.active{
    background-color:white;
    color: black;
}